import React, { Component } from 'react';
import Video from './VideoCollection.js';
import {canUseInputCapture, canRecord} from './UserAgent.js'
import './VideoGetter.css';
import './Video.css';


class WebcamDisplay extends React.Component {
  componentDidMount() {
    this.video.srcObject = this.props.stream;
  }

  shouldComponentUpdate(props) {
    return this.props.stream !== props.stream;
  }

  componentDidUpdate() {
    this.video.srcObject = this.props.stream;
  }

  render() {
    return (<video
              className="video"
              autoPlay
              muted
              ref={(video) => { this.video = video; }}></video>);
  }
}


function FileUpload(props) {
  return (
    <div className="button-container">
      <input id="newVideo" type="file"
        accept="video/*"
        onChange={props.setNewFile}
        className="newVideoInput"
        capture="camcorder" />
      <label for="newVideo" className="newVideoInputLabel button">{props.label}</label>
    </div>
  );
}


function RecordControls(props) {
  let label, onClick, className;
  if (props.recording) {
    label = "Stop Recording";
    onClick = props.stopRecording;
    className = "stop";
  } else {
    label = "Start Recording";
    onClick = props.startRecording;
    className = "start";
  }

  return (
    <div className={"button-container record-controls "+className}>
      <button className={className} onClick={onClick}>{label}</button>
    </div>
  );
}


class VideoGetter extends Component {
  constructor(props) {
    super(props);
    this.setNewFile = this.setNewFile.bind(this);
    this.startRecording = this.startRecording.bind(this);
    this.stopRecording = this.stopRecording.bind(this);
    this.startWebcamCapture = this.startWebcamCapture.bind(this);
    this.setNewVideo = this.setNewVideo.bind(this);
    this.state = {
      recording: false,
    };
    this.stream = null;
    this.mr = null;
    this.blobs = [];
  }

  startWebcamCapture() {
    let me = this;
    navigator.mediaDevices.getUserMedia({audio:true, video:true})
    .then(function(stream) {
      me.props.setNewOriginalVideo(null);
      me.props.setCapturing(true, function() { 
        me.stream = stream;
        me.forceUpdate();
      });
    })
    .catch(function(error) {
      console.error(error);
      me.props.errorHandler(
        "The webcam couldn't record; maybe you didn't give "+
        "this site permission to use it, or you don't have "+
        "a compatible webcam, or your browser doesn't support "+
        "it. Try desktop Firefox or Chrome.")
    });
  }

  startRecording() {
    try {
      this.mr = new window.MediaRecorder(
        this.stream, {mimeType:'video/webm'});
    } catch (error) {
      console.error(error);
      this.props.errorHandler(
        "Couldn't start recording for some reason. "+
        "Maybe this is an unsupported browser? Try desktop "+
        "Firefox or Chrome.");
      this.stopRecording(false);
      return;
    }

    let me = this;
    this.mr.ondataavailable = function(event) {
      if (event.data && event.data.size > 0) {
        me.blobs.push(event.data);
      }
    }
    this.mr.start();
    this.setState({recording: true})
  }

  stopRecording(setVideoBool) {
    // Might get called as a consequence of choosing "upload",
    // while recording, in which case we want the option to
    // *not* set a new video.

    if (this.mr) {
      let me = this;
      this.mr.onstop = function(event) {
        if (setVideoBool === true) {
          const blob = new Blob(me.blobs, {type: "video/webm"});
          me.setNewVideo(blob);
        }

        me.blobs = [];
        me.mr = null;
      }

      this.mr.stop();
    }

    if (this.stream) {
      this.stream.getTracks().forEach(t => t.stop());
    }

    this.setState({recording: false});
    this.props.setCapturing(false);
  }

  setNewFile(event) {
    // If we were recording, stop recording without saving.
    this.stopRecording(false);

    const files = event.target.files;
    if (files.length > 0) {
       this.setNewVideo(files[0]);
    }
  }

  setNewVideo(blob) {
    const url = URL.createObjectURL(blob);
    const video = new Video(url, "Original", blob);
    this.props.setNewOriginalVideo(video);
  }

  render() {
    return [
    <div className="button-container" key="new-vid-buttons">
      <FileUpload setNewFile={this.setNewFile}
        label={canUseInputCapture ? "New Video" : "Choose Video File"} />
      {canRecord &&
        <button className="capture"
          onClick={this.startWebcamCapture}
          disabled={this.props.capturingVideo}>
          Record Video</button>}
    </div>,
    canRecord && this.props.capturingVideo &&
      <WebcamDisplay key="recording-view" stream={this.stream} />,
    canRecord && this.props.capturingVideo &&
      <RecordControls key="record-controls"
        recording={this.state.recording}
        startRecording={this.startRecording}
        stopRecording={() => this.stopRecording(true)} />
    ];
  }
}

export default VideoGetter;


