import {canUseInputCapture, canRecord} from './UserAgent.js'

const buttonSelection = 
  canUseInputCapture
    ? '"New Video"'
    : ('"Choose Video File"' + (canRecord ? ' or "Record Video"' : ''));

const explanation =
  (!canUseInputCapture && !canRecord)
  ? "To record video, use your mobile device, or your webcam with desktop Firefox or Chrome. "
  : "";

const aboutText = `## What is this?

This is several things:

1. It's a human-to-dog translator, in the spirit of style transfer, but for audio instead of images.
2. It's a demonstration of getting a Tensorflow model running in the browser via Webassembly. That means that all the processing is happening locally on your computer; none of your private video or audio is being sent to an external server. It's also, to my knowledge, the first time someone has managed to compile a Tensorflow model to Webassembly.

## How do I use it?
Select a video with ${buttonSelection} above. ${explanation}Then click "Translate". After the processing finishes, you can listen to what you sound like translated to a dog barking. It might take a while, depending on your computer and browser. On my laptop it took roughly 10 seconds to process 10 seconds of audio, but probably a mobile phone will be slower.

## So, how does it work?
That's a big question with a big answer! I'm going to keep quiet about how the actual translation works for now, but I will talk in some depth about how I got a Tensorflow model running in the browser with Webassembly.

## Why not Tensorflow.js?
The Tensorflow team has recently announced Javascript bindings for Tensorflow. That's another solid approach to running Tensorflow models in the browser. I investigated that option, but ultimately it didn't suit my needs because I was using too many operations (for example the Fast Fourier Transform) that are still unsupported in Tensorflow.js. If you want to run your own model in the browser, and you are able to use Tensorflow.js, then you should probably prefer to use that.

## Tensorflow to Webassembly. How?
The very short version: Tensorflow's ahead-of-time (AOT) XLA compiler, which I customized to support Webassembly as a target. AOT-compiled models are not quite as flexible as executing models in the usual Tensorflow runtime, but currently they have more supported ops than Tensorflow.js models do.

The longer version includes the following topics, which I'll write about in more detail at a future time:

1. Modifying Tensorflow's AOT compiler to support Webassembly as a target
2. Modifying my Tensorflow model to support AOT compilation (avoiding unsupported ops, making tensor shapes predictable at compile time)
3. Modifying individual XLA operations to support Webassembly (such as the Fast Fourier Transform mentioned above)
4. Wrapping my compiled model with glue code and compiling it with emscripten
5. Profiling my model and solving performance issues
6. Playing a video with alternate audio tracks in the browser. (This took some doing; it's still not well-supported!)

Enter your email below if you'd like to be notified when I post more details. If you're especially interested in any of these topics, please get in touch and let me know! If you're interested in getting your own Tensorflow model to run in the browser via Webassembly, and would like to hire me to help you achieve that, also get in touch! My email is <haggai@haggainuchi.com>.
`;

export default aboutText;


