import React from 'react';
import './VideoButton.css';


function activeString(props, idx) {
  return (idx === props.activeAudioIdx)
          ? "active"
          : "inactive";
}

function getVidButton(props, name, idx) {
  return (
    <button
        key={name}
        idx={idx}
        className={"video-button "+activeString(props, idx)}
        onClick={()=>props.setActive(idx)}>
      {name}
    </button>
  );
}

function VidButtonArray(props) {
  return (
    <div className={"button-container" + (props.shouldHide ? " hidden" : "")}>
      {props.names
        .map(
          (k, idx) => getVidButton(props, k, idx)
        )}
    </div>
  );
}


export default VidButtonArray;


