import React, { Component } from 'react';
import translateVideo from './translate.js'
import './TranslateVideoButton.css'

class TranslateVideoButton extends Component {
  constructor(props) {
    super(props);
    this.handleClick = this.handleClick.bind(this);
    this.state = {
      processing: false
    };
  }

  handleClick() {
    this.setState({processing: true});
    let me = this;

    const audioSetter = function(audios) {
      me.props.audioSetter(audios);
      me.setState({processing: false});
    }

    const errorHandler = function(error) {
      me.props.errorHandler(error);
      me.setState({processing: false});
    }

    translateVideo(
      this.props.originalVideo,
      this.props.duration,
      errorHandler,
      audioSetter,
      /*progress=*/() => {});
  }

  render() {
    return (
      <div className={"button-container" + (this.props.shouldHide ? " hidden" : "")}>
        <button className="translate-button"
          disabled={this.state.processing}
          onClick={this.handleClick} >
          {this.state.processing
            ? "Processing..."
            : "Translate!"}
        </button>
      </div>
    );
  }
}

export default TranslateVideoButton;


