function canUseMediaRecorder() {
  let b = (window.MediaRecorder
           && navigator.mediaDevices
           && navigator.mediaDevices.getUserMedia);
  return !!b;
}

function canUseCameraInFileUpload() {
  return isMobile();
}

function isMobile() {
  return /Mobi/.test(navigator.userAgent);
}

export const canUseInputCapture = canUseCameraInFileUpload();
export const canRecord = !canUseInputCapture && canUseMediaRecorder();



