import React, { Component } from 'react';
import { Player,
         BigPlayButton,
         ControlBar,
         VolumeMenuButton } from 'video-react';
import "video-react/dist/video-react.css";


function vh_to_pixel(v) {
  return (window.innerHeight * v) / 100.0;
}


export default class CustomPlayer extends Component {
  constructor(props) {
    super(props);
    this.player = null;
    this.audio = null;
    this.recalculateHeight = this.recalculateHeight.bind(this);
    this.state = {
      prevVideo: null,
      fluid: true,
      height: undefined,
    }
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.originalVideo !== prevState.prevVideo) {
      return {
        prevVideo: nextProps.originalVideo
      };
    }
    return null;
  }

  componentDidUpdate(_, prevState) {
    if (prevState.prevVideo !== this.state.prevVideo) {
      if (this.player) {
        this.player.load();
      }
    }
  }

  componentDidMount() {
    this.recalculateHeight();
    this.forceUpdate();
    window.addEventListener("resize", this.recalculateHeight);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.recalculateHeight);
  }

  recalculateHeight() {
    const maxHeight = vh_to_pixel(80);
    if (this.player && this.player.videoHeight > maxHeight) {
      this.setState({
        height: maxHeight,
        fluid: false,
      });
    } else {
      this.setState({
        height: undefined,
        fluid: true,
      });
    }
  }


  render() {
    return ([
      <Player key="player"
          ref={(c) => {this.player = c}}

          onPlay={(e) => {
            this.audio.currentTime = e.target.currentTime;
            this.audio.play();
          }}
          onPause={() => {this.audio.pause()}}
          onSeeking={(e) => {this.audio.currentTime = e.target.currentTime}}
          onSeeked={(e) => {this.audio.currentTime = e.target.currentTime}}

          onLoadedMetadata={() => {this.recalculateHeight()}}
          fluid={this.state.fluid}
          height={this.state.height}
          className={this.props.className}
          muted={this.props.useAlternateAudio}
          onDurationChange={(e) => this.props.setDuration(e.target.duration)}
          src={this.props.originalVideo && this.props.originalVideo.url}>
        <BigPlayButton position="center" />
        <ControlBar>
          <VolumeMenuButton disabled />
        </ControlBar>
      </Player>
      ,
      <audio
          key="audio"
          ref={(c) => {this.audio = c}}
          src={this.props.activeAudio && this.props.activeAudio.url}
          muted={!this.props.useAlternateAudio}
          display="hidden"
          >
        </audio>
    ]);
  }
};


