import React, { Component } from 'react';
import ReactMarkdown from 'react-markdown';
import VideoView from './VideoView.js';
import VideoGetter from './VideoGetter.js';
import aboutText from './aboutText.js';
import SubscribeNotice from './SubscribeNotice.js';
import './App.css';


class App extends Component {
  constructor(props) {
    super(props);
    this.setAudios = this.setAudios.bind(this);
    this.setNewOriginalVideo = this.setNewOriginalVideo.bind(this);
    this.gcOldVideos = this.gcOldVideos.bind(this);
    this.oldUrls = [];
    this.state = {
      originalVideo: null,
      translatedAudios: [],
      capturingVideo: false,
      errorText: '',
    };
  }

  showWelcome() {
    return (this.state.originalVideo === null)
      && (this.state.capturingVideo === false);
  }

  gcOldVideos() {
    this.oldUrls.forEach(v => v && v.release());
    this.oldUrls = [];
  }

  setAudios(audios) {
    this.setState(function (prevState) {
      prevState.translatedAudios.forEach(
        v => this.oldUrls.push(v));

      return {
        translatedAudios: audios
      };
    }, this.gcOldVideos);
  }

  setNewOriginalVideo(video) {
    this.setState(function(prevState) {
      this.oldUrls.push(prevState.originalVideo);
      prevState.translatedAudios.forEach(
        v => this.oldUrls.push(v));

      return {
        originalVideo: video,
        translatedAudios: []
      };
    }, this.gcOldVideos);
  }

  render() {
    return (
      <div className="App">
        <VideoGetter
          errorHandler={e => this.setState({errorText: e})}
          capturingVideo={this.state.capturingVideo}
          setCapturing={(c, cb) => this.setState({capturingVideo: c}, cb)}
          setNewOriginalVideo={this.setNewOriginalVideo} />
        {!this.state.capturing &&
          <VideoView
            originalVideo={this.state.originalVideo}
            translatedAudios={this.state.translatedAudios}
            errorHandler={e => this.setState({errorText: e})}
            audioSetter={this.setAudios} />}
        <ReactMarkdown source={aboutText} className="welcome" />
        <SubscribeNotice />
        {!!this.state.errorText &&
          <div className="error-modal" key="error-modal"
              onClick={() => this.setState({errorText: ''})}>
            <div className="error button inactive">
              {this.state.errorText}
            </div>
          </div>}
      </div>
    );
  }
}


export default App;



