import React, { Component } from 'react';
import VidButtonArray from './VideoButtons.js';
import TranslateVideoButton from './TranslateVideoButton.js';
// import linkVideoAndAudio from './sync.js';
import CustomPlayer from './player2.js';
import './VideoView.css';
import './Video.css';


class VideoView extends Component {
  constructor(props) {
    super(props);
    this.setActive = this.setActive.bind(this);
    this.state = {
      duration: 0.0,
      useAlternateAudio: false,
      alternateAudioIdx: 0,
    };
  }

  setActive(idx) {
    if (idx === 0) {
      this.setState({useAlternateAudio: false});
    } else {
      this.setState({
        useAlternateAudio: true,
        alternateAudioIdx: idx - 1,
      });
    }
  }

  getAllAudios() {
    let audios = [this.props.originalVideo]
      .concat(this.props.translatedAudios);
    return audios;
  }

  render() {
    const shouldHide = (this.props.originalVideo === null);

    const activeAudio =
      (this.state.alternateAudioIdx < this.props.translatedAudios.length)
        ? this.props.translatedAudios[this.state.alternateAudioIdx]
        : null;

    const audios = this.getAllAudios();

    const activeAudioIdx =
      this.state.useAlternateAudio ? 1 + this.state.alternateAudioIdx : 0;

    return [
      <CustomPlayer key="video"
        className={"media-player" + (shouldHide ? " hidden" : "")}
        originalVideo={this.props.originalVideo}
        setDuration={(d) => this.setState({duration: d})}
        duration={this.state.duration}
        activeAudio={activeAudio}
        useAlternateAudio={this.state.useAlternateAudio}
        />,
      <VidButtonArray key="vid-button-array"
        setActive={this.setActive}
        names={shouldHide ? [] : audios.map(a => a.name)}
        activeAudioIdx={activeAudioIdx}
        shouldHide={(shouldHide || this.props.translatedAudios.length === 0) ? " hidden" : ""} />,
      <TranslateVideoButton key="translate-button"
        originalVideo={this.props.originalVideo}
        errorHandler={this.props.errorHandler}
        audioSetter={(...args) => {this.props.audioSetter(...args); this.setActive(1);}}
        duration={this.state.duration}
        shouldHide={(shouldHide || this.props.translatedAudios.length > 0) ? " hidden" : ""} />
    ];
  }
}


export default VideoView;


