import Video from './VideoCollection.js';
import translate from './chop.js';
import encodeWAV from './encodeWAV.js';

function translateVideo(video, duration, errorHandler, audioSetter, progressSetter) {
  let fr = new FileReader();
  let p = new Promise(function(resolve) {
    fr.onload = () => resolve(fr.result);
  });
  fr.readAsArrayBuffer(video.blob);

  p.then(function(arraybuffer) {
    const ctx = new window.AudioContext({sampleRate:16000});
    const octx = new window.OfflineAudioContext(1, 16000 * duration, 16000);
    const source = octx.createBufferSource();
    ctx.decodeAudioData(arraybuffer, function(audio) {
      source.buffer = audio;
      source.connect(octx.destination);
      source.start();
      octx.startRendering().then(function(renderedBuffer) {
        const translatedAudio = translate(renderedBuffer.getChannelData(0), progressSetter);
        const dataview = encodeWAV(translatedAudio, 16000);
        const blob = new Blob([dataview], {type: "audio/wav"});
        const url = window.URL.createObjectURL(blob);
        audioSetter([new Video(url, "Dog", blob)]);
      });
    });
  })
}


export default translateVideo;


