import React from 'react';

export default () => {
  return (
    <div id="mc_embed_signup">
      <form action="https://haggainuchi.us18.list-manage.com/subscribe/post?u=36647dbde0c4fd6c153d670bd&amp;id=6627d1f26c" method="post" id="mc-embedded-subscribe-form" name="mc-embedded-subscribe-form" className="validate" target="_blank" noValidate>
        <div id="mc_embed_signup_scroll">
          <h3>Subscribe to notifications of updates</h3>
          <div className="mc-field-group">
            <label for="mce-EMAIL">Email Address </label>
            <input type="email" defaultValue="" name="EMAIL" className="required email" id="mce-EMAIL" />
          </div>
          <div id="mce-responses" className="clear">
            <div className="response" id="mce-error-response" style={{display:"none"}}></div>
            <div className="response" id="mce-success-response" style={{display:"none"}}></div>
          </div> 
          <div style={{position: "absolute", left: "-5000px"}} aria-hidden="true">
            <input type="text" name="b_36647dbde0c4fd6c153d670bd_6627d1f26c" tabIndex="-1" value="" />
          </div>
          <div className="clear">
            <input type="submit" value="Subscribe" name="subscribe" id="mc-embedded-subscribe" className="button" />
          </div>
        </div>
      </form>
    </div>
  );
}


