function translate(inputAudio, progressSetter) {
  const arg_size = window.Module.arg_size; //65536;
  const out_size = window.Module.out_size; //55040;
  const padding = (arg_size - out_size) / 2;
  const audioLength = inputAudio.length;

  let processedSamples = 0;
  let remainingSamples = audioLength;
  let inputOffset = -padding;
  let outputAudio = new Float32Array(audioLength);

  while (remainingSamples > 0) {
    // Fill arg_data
    if (processedSamples === 0) {
      // First time gets padded by zeros
      // console.log("Setting arg_data first time");
      let thisArgData = inputAudio.subarray(0, arg_size - padding);
      window.Module.arg_data.set(thisArgData, padding);
    } else {
      // console.log("Setting arg_data subsequent time");
      let thisArgData = inputAudio.subarray(inputOffset, inputOffset + arg_size);
      window.Module.arg_data.set(thisArgData);
      if (audioLength - inputOffset < arg_size) {
        // Last time needs more zeros
        // console.log("Filling end of arg_data with zeros");
        window.Module.arg_data.fill(0.0, audioLength - inputOffset);
      }
    }

    window.Module._compute();

    let processedThisTime = Math.min(out_size, remainingSamples);
    outputAudio.set(window.Module.result_data.subarray(0, processedThisTime), processedSamples)

    inputOffset += out_size;
    processedSamples += processedThisTime;
    remainingSamples -= processedThisTime;
    // console.log("Processed " + processedSamples + " samples");
    progressSetter("~~~~ reporting progress ~~~~");
  }
  // console.log("Finished");
  return outputAudio;
}

export default translate;



